    .LeaderboardView-content {\n        margin-top: 20px;\n        .tableTitleContainer {\n            border-radius: 12px 12px 0px 0px;\n            position: relative;\n            overflow: hidden;\n            .tableTitle {\n                text-align: center;\n                margin: 0 auto;\n                padding: 8px 16px;\n            }\n            .LB-SideTournamentsDrawer{\n                $arrow_color: rgb(0 0 0 / 40%);\n                position: absolute;\n                left: 0;\n                top: 0;\n                height: 100%;\n                display: flex;\n                align-items: center;\n                justify-content: center;\n                background: #8ABBE3;\n                .SideTournamentsDrawer-expandButton {\n                    display: flex;\n                    align-items: center;\n                    justify-content: center;\n                    border-left: 3px solid $arrow_color;\n                    height: 100%;\n                    cursor: pointer;\n                    margin-left: 4px;\n                    > svg {\n                        color: $arrow_color;\n                        transition: transform 0.4s;\n                        filter: brightness(1.1);\n                    }\n                    &.expanded {\n                        > svg {\n                            transform: rotate(180deg);\n                        }\n                    }\n                }\n                .SideTournamentsDrawer-list {\n                    margin-left: 6px;\n                    margin-right: 2px;\n                    display: flex;\n                    .SideTournamentsDrawer-sideTournament{\n                        margin: 0px 12px;\n                        cursor: pointer;\n                        height: 30px;\n                        width: 30px;\n                    }\n                }\n                .SideTournamentsDrawer-backToMain{\n                    margin: 0px 12px;\n                    cursor: pointer;\n                    height: 30px;\n                    width: 30px;\n                    color: $arrow_color;\n                }\n            }\n        }\n        .MuiTableContainer-root {\n            background-color: rgba(255,255,255, 0.95);\n        }\n        .LigaBet-CustomTable {\n            border-radius: 0 0 12px 12px;\n            .MuiTable-root  {\n                border-collapse: separate;\n            }\n            .tableHeaderCell {\n                padding: 8px 16px;\n                font-size: 16px;\n                font-weight: 700;\n            }\n            .LigaBet-CustomTableRow {\n                @include themable_prize_row(prize-theme-1, $prize_1);\n                @include themable_prize_row(prize-theme-2, $prize_2);\n                @include themable_prize_row(prize-theme-3, $prize_3);\n                @include themable_prize_row(prize-theme-4, $prize_4);\n                @include themable_prize_row(prize-theme-5, $prize_5);\n                @include themable_prize_row(prize-theme-6, $prize_6);\n                cursor: pointer;\n                &.currentUtl {\n                    .MuiTableCell-root {\n                        &:first-of-type {\n                            border-right: 6px solid $primary-main;\n                            .rankChange {\n                                margin-right: -2px;\n                            }\n                        }\n                        &.nameCell {\n                            font-weight: 700;\n                        }\n                    }\n                }\n            }\n            .MuiTableCell-root {\n                font-size: 16px;\n                border-bottom-color: rgba(0,0,0,0.1);\n                &.rankChangeCell {\n                    width: 0px;\n                    padding-left: 0px;\n                    padding-right: 8px;\n                }\n                &.rankCell {\n                    width: 0px;\n                    padding-right: 8px;\n                }\n                &.scoreCell {\n                    width: 0px;\n                }\n            }\n        }\n\n        .scoreCell-container {\n            display: flex;\n            align-items: center;\n            justify-content: space-between;\n            .scoreCell-total {\n                font-size: 18px;\n            }\n            .scoreCell-added {\n                padding: 3px 4px;\n                background-color: green;\n                margin-right: 8px;\n                color: #fff;\n                border-radius: 12px;\n                line-height: 1;\n                font-size: 12px;\n                margin-top: -1px;\n                box-shadow: $medium-shadow;\n                text-shadow: none;\n                @include flexRow;\n                > span {\n                    margin-left: -1px;\n                    padding-right: 2px;\n                }\n                > .scoreCell-addIcon {\n                    font-size: 16px;\n                }\n            }\n        }\n        .rankChange {\n            @include flexRow;\n            box-shadow: $medium-shadow;\n            border-radius: 50%;    display: flex;\n            padding-right: 6px;\n            position: relative;\n            display: flex;\n            align-items: center;\n            border-radius: 20px;\n            margin-top: -3px;\n            background-color: #eee;\n            margin-right: 4px;\n            text-shadow: none;\n            height: 22px;\n            .rankChange-value {\n                font-size: 14px;\n                line-height: 1;\n                color: #000;\n                margin-left: -2px;\n            }\n            .rankChange-direction {\n                color: $success-main;\n                transform: rotate(180deg);\n            }\n            &.isNegative {\n                .rankChange-direction {\n                    color: $error-main;\n                    transform: rotate(0deg);\n                }\n            }\n            \n        }\n    }\n}\n\n.LB-NameWithWinnerFlag {\n    @include flexRow;\n    height: 24px;\n    margin-top: -4px;\n    .NameWithWinnerFlag-name {\n        margin-right: 8px;\n        margin-bottom: -1px;\n    }\n    .NameWithWinnerFlag-noWinner {\n        width: 32px;\n        height: 32px;\n    }\n}\n\n\n.LB-ExpanededContestantView {\n    .LB-SpecialBetsTable, .LB-GameBetsTable, .LB-GroupRankBetsTable {\n        .MuiTableContainer-root {\n            background-color: rgb(255 255 255 / 95%);\n            border-radius: 12px;\n            @media (max-width: 600px) {\n                .MuiTableCell-root {\n                    padding: 8px\n                }\n            }\n            .MuiTableCell-root {\n                &.alignToTop {\n                    vertical-align: top;\n                }\n                &.scoreCell {\n                    font-size: 18px;\n                    text-align: center;\n                }\n            }\n        }\n    }\n    @media (max-width: 500px) {\n        .MuiTableContainer-root {\n            width: calc(100% + 16px);\n            margin-right: -8px;\n        }\n    }\n    .hisBetsLink {\n        margin-bottom: 12px;\n        @include centerContainer;\n        .MuiLink-root {\n            cursor: pointer;\n        }\n    }\n}\n\n.LB-SideTournamentTitle {\n    @include centerContainer;\n    .SideTournamentTitle-emblem {\n        margin-left: 16px;\n        height: 18px;\n        width: 18px;\n        transform: scale(1.5);\n    }\n}","@import './shadows';\n\n\n@mixin centerContainer {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n}\n\n@mixin flexRow {\n    display: flex;\n    align-items: center;\n}\n\n@mixin fullView {\n    height: 100%;\n    width: 100%;\n}\n\n@mixin floatingFrame {\n    box-shadow: $basic-shadow;\n    background-color: rgba(255,255,255, 0.95);\n}\n\n@mixin headerText {\n    text-shadow: -3px 2px 8px black;\n    color: white;\n}\n\n@mixin floatingTable {\n    background-color: rgb(255 255 255 / 95%);\n    border-radius: 12px;\n}","$basic-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em, rgba(90, 125, 188, 0.05) 0px 0.25em 1em;\n$medium-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;","/* -- MUI's Default Color Palette -- */\n\n// Primary\n$primary-light: #42a5f5;\n$primary-main: #1976d2;\n$primary-dark: #1565c0;\n$primary-light-main-gradient: linear-gradient(\n    45deg,\n    $primary-light 0%,\n    $primary-main 100%\n);\n\n// Secondary\n$secondary-light: #ba68c8;\n$secondary-main: #9c27b0;\n$secondary-dark: #7b1fa2;\n\n// Error\n$error-light: #ef5350;\n$error-main: #d32f2f;\n$error-dark: #c62828;\n\n// Warning\n$warning-light: #ff9800;\n$warning-main: #ed6c02;\n$warning-dark: #e65100;\n\n// Info\n$info-light: #03a9f4;\n$info-main: #0288d1;\n$info-dark: #01579b;\n\n// Success\n$success-light: #4caf50;\n$success-main: #2e7d32;\n$success-dark: #1b5e20;\n\n\n$purple-light: #982dd6;\n$purple-main: #6f1ba0;\n$purple-dark: #4b1968;"],"sourceRoot":""}]);
