to;\n            overflow-x: hidden;\n            padding-top: $data_section_padding_top;\n            .DigramDataSection-markRow {\n                position: absolute;\n                height: $progress_row_height;\n                left: 0;\n                width: 100%;\n                background: rgb(0 0 255 / 6%);\n                border-radius: 12px;\n                transform: translateY($data_section_padding_top);\n                transition: $topTransition;\n            }\n            .usersSection {\n                position: relative;\n                max-width: 35%;\n                .LB-ProgressRowUserDetails {\n                    @include flexRow;\n                    transition: $topTransition;\n                    margin-bottom: 8px;\n                    position: absolute;\n                    height: $progress_row_height;\n                    white-space: nowrap;\n                    .ProgressRowUserDetails-userName {\n                        flex: 1 auto;\n                        $margin: 8px;\n                        font-weight: 700;\n                        margin-right: $margin;\n                    }\n                }\n            }\n            .LB-ProgressDiagramScoresSection {\n                padding-right: 36px;\n                padding-left: 8px;\n                flex: 1 auto;\n                height: 100%;\n                position: relative;\n                .ProgressDiagramScoresSection-content {\n                    position: relative;\n                    height: 100%;\n                }\n                .LB-ScoresBar {\n                    transition: $topTransition;\n                    width: 100%;\n                    position: absolute;\n                    display: flex;\n                    flex-direction: row-reverse;\n                    align-items: center;\n                    height: $progress_row_height;\n                    margin-bottom: 8px;\n                    .ScoresBar-bar {\n                        height: 20px;\n                        min-width: 20px;\n                        padding: 0px 4px;\n                        border-radius: 8px;\n                        transition: width 1s linear;\n                        display: flex;\n                        align-items: center;\n                        .ScoresBar-addedScore {\n                            color: #fff;\n                            font-size: 14px;\n                            font-weight: 700;\n                            white-space: nowrap;\n                            max-width: 100%;\n                        }\n                        .ScoresBar-scores {\n                            position: absolute;\n                            transform: translateX(calc(10px + 100%));\n                            line-height: 16px;\n                            font-size: 16px;\n                            font-weight: 700,\n                        }\n                    }\n                }\n            }\n            .yaxes{\n                position: sticky;\n                top: 0px;\n            }\n            .LB-YAxes {\n                $color: #562b0866;\n                position: absolute;\n                height: 100%;\n                width: 0px;\n                border-left: 1px dashed $color;\n                transition:  left 0.7s linear;\n                > span {\n                    color: $color;\n                    transform: translateX(50%) translateY(-100%);\n                    position: absolute;\n                }\n            }\n        }\n        .LB-DiagramVersionsDisplay {\n            width: 100%;\n            height: 100px;\n            position: relative;\n            padding: 12px 0px;\n            margin-bottom: 8px;\n            margin-left: 0;\n            transform: translateX(0);\n            transition: transform 1s linear;\n            &.emptyVersions {\n                transform: translateX(50%);\n            }\n            .spaceKeeper {\n                height: 74px;\n        