solute;\n                &.sideLeft {\n                    left: 12px;\n                }\n                &.sideRight {\n                    right: 12px;\n                }\n            }\n            .verticalTeamName {\n                margin-right: 0px;\n                margin-top: 6px;\n                text-align: center;\n                font-size: 12px;\n            }\n        }\n    }\n}","@import './shadows';\n\n\n@mixin centerContainer {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n}\n\n@mixin flexRow {\n    display: flex;\n    align-items: center;\n}\n\n@mixin fullView {\n    height: 100%;\n    width: 100%;\n}\n\n@mixin floatingFrame {\n    box-shadow: $basic-shadow;\n    background-color: rgba(255,255,255, 0.95);\n}\n\n@mixin headerText {\n    text-shadow: -3px 2px 8px black;\n    color: white;\n}\n\n@mixin floatingTable {\n    background-color: rgb(255 255 255 / 95%);\n    border-radius: 12px;\n}","$basic-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em, rgba(90, 125, 188, 0.05) 0px 0.25em 1em;\n$medium-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;","/* -- MUI's Default Color Palette -- */\n\n// Primary\n$primary-light: #42a5f5;\n$primary-main: #1976d2;\n$primary-dark: #1565c0;\n$primary-light-main-gradient: linear-gradient(\n    45deg,\n    $primary-light 0%,\n    $primary-main 100%\n);\n\n// Secondary\n$secondary-light: #ba68c8;\n$secondary-main: #9c27b0;\n$secondary-dark: #7b1fa2;\n\n// Error\n$error-light: #ef5350;\n$error-main: #d32f2f;\n$error-dark: #c62828;\n\n// Warning\n$warning-light: #ff9800;\n$warning-main: #ed6c02;\n$warning-dark: #e65100;\n\n// Info\n$info-light: #03a9f4;\n$info-main: #0288d1;\n$info-dark: #01579b;\n\n// Success\n$success-light: #4caf50;\n$success-main: #2e7d32;\n$success-dark: #1b5e20;\n\n\n$purple-light: #982dd6;\n$purple-main: #6f1ba0;\n$purple-dark: #4b1968;"],"sourceRoot":""}]);
